
.md-dialog {
    background-color: #fff;
    display: block;
}
.md-dialog-container {
    width: 100%;
    height: 100%;
    background: rgba(49, 48, 49, 0.2);
    position: fixed;
    top: 0;
    z-index: 1000;
    overflow: hidden;
}





