





































































/*========== material input ==========*/
.input-control[data-v-37ee7588] {
    position: relative;
    width: 100%;
    margin-top: 10px;
    margin-bottom: 20px;
}
.show-hide-password[data-v-37ee7588] {
    position: absolute;
    top: 0;
    right: 0;
}
i[data-v-37ee7588] {
    -webkit-user-select: none; /* Chrome all / Safari all */
    -moz-user-select: none; /* Firefox all */
    -ms-user-select: none; /* IE 10+ */
    user-select: none; /* Likely future */
}
.material-input[data-v-37ee7588] {
    background: inherit;
    z-index: 10;
    width: 100%;
    font-size: 13px;
    display: inline-block;
    border: none;
    padding-bottom: 5px;
    margin-right: 5px;
    border-bottom: 1px solid #757575;
}
.material-input[data-v-37ee7588]:focus {
    outline: none;
}
.material-label[data-v-37ee7588] {
    margin: 0;
    top: 0;
    font-size: 16px;
    transition: 0.7s cubic-bezier(0.25, 0.8, 0.5, 1);
    z-index: 10;

    position: absolute
}
.material-input:focus ~ .material-label[data-v-37ee7588], .active ~ .material-label[data-v-37ee7588] {
    -webkit-transform: translateY(-18px) translateX(-5px) scale(1);
    transform: translateY(-18px) translateX(-4px) scale(1);
    font-size: 14px;
    /*left:4px;*/
    margin-left: 4px;
    color: #1e81f0;
}
.bar[data-v-37ee7588] {
    z-index: 10;

    position: relative;
    display: block;
}
.bar[data-v-37ee7588]:before, .bar[data-v-37ee7588]:after {
    content: '';
    height: 1px;
    width: 0;
    /* bottom: 1px; */
    position: absolute;
    background: #1e81f0;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}
.bar[data-v-37ee7588]:before {
    left: 50%;
}
.bar[data-v-37ee7588]:after {
    right: 50%
}

/* active state */
.material-input:focus ~ .bar[data-v-37ee7588]:before, .material-input:focus ~ .bar[data-v-37ee7588]:after {
    width: 50%;
}
.input-error[data-v-37ee7588] {
    position: relative;
    transform: scaleY(1) translateY(-10px);
    z-index: -10;
    height: 15px;
    opacity: 0;
    color: #ff5733;
    transition: 0.7s cubic-bezier(0.25, 0.8, 0.5, 1);
}
.material-input.no-validate[data-v-37ee7588] {
    color: #ff0300;
    border-bottom: 1px solid #ff0300 !important;
}
.no-validate ~ .bar[data-v-37ee7588]:before, .no-validate ~ .bar[data-v-37ee7588]:after {
    background: #ff5733;
}
.no-validate ~ .material-label[data-v-37ee7588] {
    color: #ff5733
}
.no-validate ~ .input-error[data-v-37ee7588] {
    transform: scaleY(1) translateY(0);

    transition: 0.7s cubic-bezier(0.25, 0.8, 0.5, 1);
    z-index: 10;
    opacity: 1;
    height: auto;
    /*width: auto;*/
}
.no-validate + .material-label[data-v-37ee7588] {
    color: #ff5733 !important;
}



